import { ISwitcher } from '../components/main/popups/settings-popup/swither/switcher';

interface TKey {
  [key: string]: Tvalue;
}

interface Tvalue {
  [key: string]: string | ISwitcher;
}

export enum TList {
  header = 'header',
  moves = 'moves',
  time = 'time',
  collect = 'collect',
  results = 'results',
  settings = 'settings',
}

export const TRANSLATE: TKey = {
  header: {
    restart: '',
    stop: '',
    results: '',
    settings: '',
  },
  moves: {
    Moves: '',
  },
  time: {
    Time: '',
  },
  collect: {
    collect_puzzle: ' ',
  },
  results: {
    Your_Highest_Scores: ' ',
  },
  settings: {
    themeRU: {
      title: '',
      values: ['', ''],
    },
    themeEN: {
      title: 'Theme',
      values: ['Light', 'Dark'],
    },
    animRU: {
      title: '',
      values: ['', ''],
    },
    animEN: {
      title: 'Animation',
      values: ['On', 'Off'],
    },
    langRU: {
      title: '',
      values: ['EN', ''],
    },
    langEN: {
      title: 'Language',
      values: ['EN', ''],
    },
    soundRU: {
      title: '',
      values: ['', ''],
    },
    soundEN: {
      title: 'Sound',
      values: ['On', 'Off'],
    },
    modeRU: {
      title: '',
      values: ['', ''],
    },
    modeEN: {
      title: 'Mode',
      values: ['Default', 'Extreme'],
    },
  },
};

export function correctTranslater(lang: boolean, key: TList): Array<string> {
  const result: Array<string> = [];

  if (lang) {
    result.push(...Object.keys(TRANSLATE[key]));
    return removeUnderscore(result);
  } else {
    result.push(...(Object.values(TRANSLATE[key]) as Array<string>));
  }

  // We have to remove the underscore and put one space in front of it
  function removeUnderscore(arr: Array<string>): Array<string> {
    if (!arr.join('').includes('_')) {
      return arr;
    } else {
      return arr.map((el: string) => {
        return el
          .split('')
          .map((el, i, arrMap) => {
            if (arrMap[i + 1] === '_') {
              return (el = el + ' ');
            }
            return el;
          })
          .filter((el: string) => {
            return el !== '_';
          })
          .join('');
      });
    }
  }

  return result;
}
